import React, { Component } from 'react'
import {
	Tabs,
	Tab,
	Grid,
	Cell,
	Card,
	CardTitle,
	CardText,
	CardActions,
	Button,
	CardMenu,
	IconButton,
} from 'react-mdl'

class Projects extends Component {
	constructor(props) {
		super(props)
		this.state = { activeTab: 0 }
	}

	toggleCategories() {
		if (this.state.activeTab === 0) {
			return (
				<div className='projects-grid'>
					{/* Project 1 */}
					<Card shadow={5} className='card-item'>
						<CardTitle className='card-title'>React Project #1</CardTitle>
						<CardText>
							Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the
							industry's
						</CardText>
						<CardActions border>
							<Button colored>GitHub</Button>
							<Button colored>CodePen</Button>
							<Button colored>Live Demo</Button>
						</CardActions>
						<CardMenu style={{ color: '#fff' }}>
							<IconButton name='share' />
						</CardMenu>
					</Card>

					{/* Project 2 */}
					<Card shadow={5} className='card-item'>
						<CardTitle className='card-title'>React Project #2</CardTitle>
						<CardText>
							Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the
							industry's
						</CardText>
						<CardActions border>
							<Button colored>GitHub</Button>
							<Button colored>CodePen</Button>
							<Button colored>Live Demo</Button>
						</CardActions>
						<CardMenu style={{ color: '#fff' }}>
							<IconButton name='share' />
						</CardMenu>
					</Card>

					{/* Project 3 */}
					<Card shadow={5} className='card-item'>
						<CardTitle className='card-title'>React Project #3</CardTitle>
						<CardText>
							Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the
							industry's
						</CardText>
						<CardActions border>
							<Button colored>GitHub</Button>
							<Button colored>CodePen</Button>
							<Button colored>Live Demo</Button>
						</CardActions>
						<CardMenu style={{ color: '#fff' }}>
							<IconButton name='share' />
						</CardMenu>
					</Card>
				</div>
			)
		} else if (this.state.activeTab === 1) {
			return (
				<div>
					<h1>Projects Javascript Vanilla, HTML5, CSS3</h1>
				</div>
			)
		}
	}

	render() {
		return (
			<div>
				<Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
					<Tab>React</Tab>
					<Tab>Javascript Vanilla</Tab>
				</Tabs>

				<Grid>
					<Cell col={12}>
						<div className='content'>{this.toggleCategories()}</div>
					</Cell>
				</Grid>
			</div>
		)
	}
}

export default Projects
